@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

.pg-02 {
    font-family: 'Poppins', sans-serif;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0 6%;
    background-color: #111111;
    transition: background-color 0.5s ease;
}

.inner-pg-02 {
    display: flex;
    flex-direction: column;
    padding: 2rem 2%;
    width: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
    overflow: -moz-scrollbars-none;
    -ms-overflow-style: none;
}

.inner-pg-02::-webkit-scrollbar {
    width: 0!important;
}

.skill {
    position: fixed;
    /* left: 0; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 84.6vw;
    height: max-content;
}

.skill .skillname, .project-lnk {
    /* position: fixed; */
    font-size: 1.2rem;
    font-weight: 400;
    letter-spacing: 1.2px;
    color: aliceblue;
    width: max-content;
}

.skill .project-lnk {
    margin-bottom: 0.5rem;
}

.inner-pg-02 .skill-content {
    display: flex;
    flex-direction: column;
    margin-top: 40px;
}

.inner-pg-02 .skill-content .skl-link {
    color: #fff;
    text-decoration: none;
    display: block;
    line-height: 1.1;
    letter-spacing: -0.5rem;
    margin-top: 30px;
    width: max-content;
}


.inner-pg-02 .skill-content .skl-link .skl-name {
    font-size: 14rem;
    font-weight: 700;
}

.inner-pg-02 .skill-content .loop__clone {
    transform: translateX(0)!important;
    transition: none!important;
}

@media screen and (max-width: 1370px) {
    .skill .skillname, .project-lnk {
        font-size: 1.15rem;
    }

    .inner-pg-02 .skill-content .skl-link {
        color: #fff;
        text-decoration: none;
        display: block;
        line-height: 1.1;
        letter-spacing: -0.5rem;
        margin-top: 30px;
        width: max-content;
    }

    .inner-pg-02 .skill-content .skl-link .skl-name {
        font-size: 10rem;
    }
}

@media screen and (max-width: 955px) {
    .skill .skillname, .project-lnk {
        font-size: 1.05rem;
    }

    .inner-pg-02 .skill-content .skl-link {
        letter-spacing: -0.2rem;
    }

    .inner-pg-02 .skill-content .skl-link .skl-name {
        font-size: 8rem;
    }
}

@media screen and (max-width: 798px) {
    .inner-pg-02 .skill-content .skl-link .skl-name {
        font-size: 6.5rem;
    }
}

@media screen and (max-width: 648px) {
    .skill .skillname, .project-lnk {
        font-size: 1rem;
    }

    .inner-pg-02 .skill-content .skl-link {
        letter-spacing: -0.15rem;
        margin-top: 20px;
    }

    .inner-pg-02 .skill-content .skl-link .skl-name {
        font-size: 4.6rem;
    }
}

@media screen and (max-width: 452px) {
    .skill .skillname, .project-lnk {
        font-size: 0.85rem;
    }

    .inner-pg-02 .skill-content .skl-link {
        letter-spacing: -0.1rem;
    }

    .inner-pg-02 .skill-content .skl-link .skl-name {
        font-size: 3.8rem;
    }
}

@media screen and (max-width: 375px) {
    .inner-pg-02 .skill-content .skl-link {
        letter-spacing: -0.05rem;
    }

    .inner-pg-02 .skill-content .skl-link .skl-name {
        font-size: 3rem;
    }
}

