/* @import url('https://fonts.googleapis.com/css2?family=Anton&display=swap'); */
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

.pg-03 {
    font-family: 'Poppins', sans-serif;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 1% 6%;
    background-color: #111111;
    transition: background-color 0.5s ease;
}

.inner-pg-03 {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 2%;
    height: 100vh;
    width: 100vw;
}

.projects {
    position: fixed;
    display: flex;
    justify-content: space-between;
    width: 85vw;
    height: max-content;
    z-index: 1;
}

.projects .contact-lnk {
    margin-bottom: 0.5rem;
}

.projects .projectName, .contact-lnk {
    font-size: 1.2rem;
    font-weight: 400;
    letter-spacing: 1.2px;
    color: aliceblue;
}


.inner-pg-03 .project-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: inherit;
    margin-top: 40px;
}

.inner-pg-03 .project-content .pro-link {
    color: #fff;
    text-decoration: none;
    display: block;
    line-height: 2;
    letter-spacing: 0.3rem;
    margin-top: 30px;
    width: max-content;
    z-index: 1;
    cursor: pointer;
}

.inner-pg-03 .project-content .pro-link .pro-name {
    font-family: 'Anton', sans-serif;
    font-size: 3rem;
    font-weight: 200;
    text-transform: uppercase;
}

.inner-pg-03 .project-content .pro-link .pro-name-01::before {
    content: '01';
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
    letter-spacing: 0.2rem;
    margin-right: 12px;
}

.inner-pg-03 .project-content .pro-link .pro-name-02::before {
    content: '02';
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
    letter-spacing: 0.2rem;
    margin-right: 12px;
}

.inner-pg-03 .project-content .pro-link .pro-name-03::before {
    content: '03';
    font-size: 1rem;
    font-family: 'Poppins', sans-serif;
    letter-spacing: 0.2rem;
    margin-right: 12px;
}

.project-preview-container {
    position: absolute;
    top: 0;
    left: 0;
    height: inherit;
    width: inherit;
    filter: blur(2px);
    z-index: 0;
}

.project-preview-container .project-preview .prev {
    position: absolute;
    width: 35%;
    border-radius: 4px;
    box-shadow: rgba(0, 0, 0, 0.1) 4px 6px 2px;
}

.project-preview-container .project-preview .pro-preview-vim {
    top: calc(100% - 25%);
    left: calc(100vw - 45%);
}

.project-preview-container .project-preview .pro-preview-vim-2 {
    top: calc(100% - 80%);
    left: calc(100% - 90%);
}

.project-preview-container .project-preview .pro-preview-adorway {
    top: calc(100% - 42%);
    left: calc(100% - 92%);
    opacity: 0.6;
}

.project-preview-container .project-preview .pro-preview-adorway-2 {
    top: calc(100% - 80%);
    left: calc(100% - 40%);
    opacity: 0.6;
}

.project-preview-container .project-preview .pro-preview-el {
    width: 12%;
    top: calc(100% - 60%);
    left: calc(100% - 28%);
    opacity: 0.4;
}

.project-preview-container .project-preview .pro-preview-el-2 {
    width: 19%;
    top: calc(100% - 80%);
    left: calc(100% - 80%);
    opacity: 1;
}

.project-preview-container .project-preview .pro-preview-el-3 {
    top: calc(100% - 61%);
    left: calc(100% - 94%);
    opacity: 0.4;
}

.show {
    display: block;
}

.hide {
    display: none;
}

.active {
    color: #fff;
}

.not-active {
    color: transparent;
    -webkit-text-stroke: 2px #ffffff;
    -webkit-text-fill-color: transparent;
}

@media screen and (max-width: 768px ) {
    .projects .projectName, .contact-lnk {
        font-size: 1.05rem;
    }

    .inner-pg-03 .project-content .pro-link {
        letter-spacing: 0.22rem;
        margin-top: 20px;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name {
        font-size: 2.2rem;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-01::before {
        font-size: 0.8rem;
        letter-spacing: 0.18rem;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-02::before {
        font-size: 0.8rem;
        letter-spacing: 0.18rem;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-03::before {
        font-size: 0.8rem;
        letter-spacing: 0.18rem;
    }

    .project-preview-container .project-preview .prev {
        width: 38%;
    }

    .project-preview-container .project-preview .pro-preview-adorway {
        top: calc(100% - 42%);
        left: calc(100% - 92%);
    }
    
    .project-preview-container .project-preview .pro-preview-adorway-2 {
        top: calc(100% - 80%);
        left: calc(100% - 45%);
    }

    .project-preview-container .project-preview .pro-preview-el {
        width: 12%;
    }
    
    .project-preview-container .project-preview .pro-preview-el-2 {
        width: 29%;
    }
}

@media screen and (max-width: 585px ) {
    .inner-pg-03 .project-content .pro-link {
        letter-spacing: 0.2rem;
        margin-top: 18px;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name {
        font-size: 1.6rem;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-01::before {
        font-size: 0.65rem;
        letter-spacing: 0.18rem;
        margin-right: 10px;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-02::before {
        font-size: 0.65rem;
        letter-spacing: 0.18rem;
        margin-right: 10px;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-03::before {
        font-size: 0.65rem;
        letter-spacing: 0.18rem;
        margin-right: 10px;
    }
}

@media screen and (max-width: 425px ) {
    .projects .projectName, .contact-lnk {
        font-size: 0.85rem;
    }

    .inner-pg-03 .project-content .pro-link {
        letter-spacing: 0.2rem;
        margin-top: 18px;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name {
        font-size: 1rem;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-01::before {
        font-size: 0.65rem;
        letter-spacing: 0.18rem;
        margin-right: 8px;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-02::before {
        font-size: 0.65rem;
        letter-spacing: 0.18rem;
        margin-right: 8px;
    }
    
    .inner-pg-03 .project-content .pro-link .pro-name-03::before {
        font-size: 0.65rem;
        letter-spacing: 0.18rem;
        margin-right: 8px;
    }

    .project-preview-container .project-preview .prev {
        width: 50%;
    }

    .project-preview-container .project-preview .pro-preview-vim {
        top: calc(100% - 25%);
        left: calc(100vw - 60%);
    }
    
    .project-preview-container .project-preview .pro-preview-vim-2 {
        top: calc(100% - 80%);
        left: calc(100% - 90%);
    }

    .project-preview-container .project-preview .pro-preview-adorway {
        top: calc(100% - 32%);
        left: calc(100% - 92%);
    }
    
    .project-preview-container .project-preview .pro-preview-adorway-2 {
        top: calc(100% - 80%);
        left: calc(100% - 60%);
    }

    .project-preview-container .project-preview .pro-preview-el {
        width: 15%;
        top: calc(100% - 60%);
        left: calc(100% - 48%);
    }
    
    .project-preview-container .project-preview .pro-preview-el-2 {
        width: 32%;
        top: calc(100% - 70%);
        left: calc(100% - 80%);
    }

    .project-preview-container .project-preview .pro-preview-el-3 {
        top: calc(100% - 35%);
        left: calc(100% - 94%);
        opacity: 0.4;
    }
}



