@import url('https://fonts.googleapis.com/css2?family=Vibur&display=swap');
@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap');

/* page 1 */
.pg-01 {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4%;
    background-color: #111111;
}

.inner-pg-01 {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    /* margin-bottom: 20px; */
}

.title {
    width: 100%;
    height: 128px;
    position: relative;
    display: flex;
    align-items: center;
}

.titlebg {
    width: 0%;
    height: inherit;
    position: absolute;
    display: flex;
    background-color: #FFB72B;
    z-index: 1;
    /* animation: reveal 2s cubic-bezier(.74, .08, .4, 1) forwards; */
}

.own-name {
    font-family: 'Poppins', sans-serif;
    cursor: default;
    font-size: 8rem;
    color: #ffffff;
    line-height: 1;
    margin-bottom: 0;
    position: relative;
    display: flex;
    align-items: baseline;
    /* opacity: 0; */
    background-clip: border-box;
    /* animation: FadeIn 1.8s 0.4s forwards; */
}

h1 span .rubber {
    display: inline-block;
    transition: all 0.2s ease-out;
}

span.break::before {
    content: ' ';
    white-space: pre;
}

h1 span .rubber:hover {
    color: #9153F4;
    /* color: #F6D743; */
}

.animate {
    animation-name: rubberBand;
    animation-duration: 1s;
    animation-fill-mode: both;
}

.own-name .letend {
    color: #FFB72B;
    border-radius: 50px;
    transform: scale(0);
    animation: scale 1s 1.4s forwards;
}

.role {
    width: 100%;
    height: 40px;
    margin-top: 8px;
    position: relative;
    display: flex;
    align-items: center;
}

.own-role {
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    letter-spacing: 0.15rem;
    font-size: 1.2rem;
    font-weight: 300;
    color: #D6C5F0;
    margin-bottom: 0;
}

.btn-con {
    font-family: 'Poppins', sans-serif;
    display: flex;
    flex-direction: column;
    width: 8rem;
    height: 2.65rem;
    border-radius: 16px;
    margin-top: 18px;
    background-color: #11111100;
    z-index: 0;
}

.btn-con a {
    width: inherit;
    height: inherit;
    letter-spacing: 1px;
    color: #111111;
    background-color: #11111100;
    border: 1px solid #fff;
    border-radius: 1px;

    font-weight: 300;
    text-align: center;
    text-decoration: none;
    line-height: 2.6;
    outline: 0;
    cursor: pointer;
    position: relative;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    transition: 0.3s ease;
}

.btn-con a:hover {
    border: 1px solid #FFB72B;
    transition: 0.3s ease;
}

.btn-con a:after {
    content: "";
    background-color: #FFB72B;
    width: 100%;
    z-index: -1!important;
    position: absolute;
    height: 100%;
    top: 7px;
    left: 7px;
    transition: 0.3s;
}

.btn-con a:hover:after {
    top: 0px;
    left: 0px;
}

/* blob */
.background-blob {
    display: none;
    position: absolute;
    border-radius: 100%;
    background: #7F00FF;
    background: -webkit-linear-gradient(to right, #E100FF, #7F00FF); 
    background: linear-gradient(to right, #E100FF, #7F00FF);
    /* visibility: hidden; */
    animation: blob 5.5s linear infinite;
    /* animation: bubbles 5.5s linear infinite; */
}

.bb1 {
    width: 700px;
    height: 700px;
    transition: all 3s ease;
}

@keyframes blob {
    0%, 100% {
        border-radius: 40% 60% 70% 30% / 40% 40% 60% 50%;
    }
    34% {
        border-radius: 70% 30% 50% 50% / 30% 30% 70% 70%;
    }
    67% {
        border-radius: 100% 60% 60% 100% / 100% 100% 60% 60%;
    }
}

@keyframes reveal {
    0% {
        width: 0%;
        left: 0;
    }
    50% {
        width: 100%;
        left: 0;
    }
    100% {
        width: 0;
        left: 100%;
    }
}
  
@keyframes FadeIn {
    0% {
        opacity: 0;
    }
    40% {
        opacity: 0;
    }
    80% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes scale {
    0% {
        opacity: 0;
        transform: scale(0);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes rubberBand {
    from {
      transform: scale3d(1, 1, 1);
    }
  
    30% {
      transform: scale3d(1.15, 0.65, 1);
    }
  
    40% {
      transform: scale3d(0.65, 1.15, 1);
    }
  
    50% {
      transform: scale3d(1.05, 0.75, 1);
    }
  
    65% {
      transform: scale3d(.85, 0.95, 1);
    }
  
    75% {
      transform: scale3d(0.95, .85, 1);
    }
  
    to {
      transform: scale3d(1, 1, 1);
    }
}

@keyframes scaleUp {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}

@keyframes popIn {
    0% {
        opacity: 0;
        width: 0px;
        height: 0px;
        fill: #9153F4;
    }
    50% {
        opacity: 1;
        width: 1rem;
        height: 1rem;
        bottom: 45px;
        fill: #9153F4;
    }
    65% {
        width: 0.5rem;
        height: 0.5rem;
        bottom: 0px;
    }
    80% {
        width: 1rem;
        height: 1rem;
        bottom: 20px
    }
    90% {
        width: 2rem;
        height: 2rem;
        bottom: 10px
    }
    95% {
        width: 2.8rem;
        height: 2.9rem;
        bottom: 6px
    }
    98% {
        width: 3.2rem;
        height: 3.2rem;
        bottom: 2px
    }
    100% {
        width: 3.75rem;
        height: 3.75rem;
        fill: #9153F4;
    }
}

@media screen and (max-width: 1200px) {
    .pg-01 {
        padding: 5%;
    }

    .inner-pg-01 {
        margin-bottom: 10px;
    }
    
    .title {
        height: 99.2px;
    }
    
    .own-name {
        font-size: 6.2rem;
    }

    .role {
        height: 28.8px;
    }

    .own-role {
        font-size: 1rem;
    }

}

@media screen and (max-width: 900px) {
    .pg-01 {
        padding: 4%;
        flex-wrap: wrap;
    }

    .inner-pg-01 {
        max-width: 100%;
        margin-bottom: 10px;
        flex-wrap: wrap;
    }
    
    .title {
        height: 83.2px;
    }
    
    .own-name {
        max-width: 100%;
        flex-wrap: wrap;
        font-size: 5.2rem;
    }

    .role {
        height: 27.8px;
    }

    .own-role {
        font-size: 0.9rem;
    }

    .sound-global {
        top: 84%;
        left: 85%;
        width: 3rem;
        height: 3rem;
    }
}

@media screen and (max-width: 768px) {
    .btn-con {
        width: 6rem;
        height: 2.25rem;
    }

    .btn-con a {
        font-size: 0.9rem;
    }
}

@media screen and (max-width: 700px) {
    .pg-01 {
        padding: 7%;
    }

    .inner-pg-01 {
        margin-bottom: 10px;
    }
    
    .title {
        height: 240px;
    }
    
    .own-name {
        font-size: 7.5rem;
    }

    .role {
        height: 27.8px;
    }

    .own-role {
        font-size: 0.9rem;
    }

    h1 span.break::before {
        content: ' ';
        white-space: normal;
    }

}

@media screen and (max-width: 550px) {
    .pg-01 {
        padding: 6.8%;
    }

    .title {
        height: 192px;
    }
    
    .own-name {
        font-size: 6rem;
    }

    .own-role {
        font-size: 0.8rem;
    }
}

@media screen and (max-width: 425px) {
    .pg-01 {
        padding: 6.8%;
    }

    .title {
        height: 144px;
    }
    
    .own-name {
        font-size: 4.5rem;
    }

    .own-role {
        font-size: 0.6rem;
    }

    .sound-global {
        top: 86%;
        left: 78%;
    }
}

@media screen and (max-width: 768px) {
    .btn-con {
        width: 5rem;
        height: 2rem;
    }

    .btn-con a {
        font-size: 0.75rem;
    }
}




  