@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

.pg-04 {
    font-family: 'Poppins', sans-serif;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    padding: 0 6%;
    background-color: #FFB72B;
    transition: background-color 0.5s ease;
}

.inner-pg-04 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 5rem 5%;
    border-radius: 5px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    height: max-content;
    width: max-content;
}

.contact {
    height: max-content;
    width: max-content;
}

.contact .contactHead {
    font-size: 5.5rem;
    color: #fff;
    margin-bottom: 0;
}

.contact-content {
    display: flex;
    flex-direction: row;
}

.contact-content .social-lnk {
    display: flex;
    text-decoration: none;
    width: 4.5rem;
    height: 4.5rem;
    margin: 5px;
}

.contact-content .social-lnk svg {
    height: inherit;
    width: inherit;
    /* fill: #FFB72B; */
    fill: #1d1d1d;
}

.form-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0.5%;
    width: max-content;
    height: max-content;
    margin-top: 15px;
}

.form-container .contact-me {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: max-content;
    height: max-content;
}

.form-container .contact-me .row {
    display: flex;
}

.form-container .contact-me .row .column {
    margin: 0 50px 50px 50px;
}

.cl-1 {
    margin-right: 25px!important;
}

.cl-2 {
    margin-left: 25px!important;
}

.form-container .contact-me .row .column .form-group {
    display: flex;
    flex-direction: column;
}

.form-container .contact-me .row .column .form-group label {
    font-size: 1.2rem;
}

.form-container .contact-me .row .column .form-group input {
    height: 60px;
    width: 220px;
    padding: 0 10px;
    border: none;
    border-radius: 4px;
}

.form-container .contact-me .row .column .form-group label {
    margin-top: 20px;
    color: #000;
}

.form-container .contact-me .row .column .form-group textarea {
    width: 260px;
    height: 178px;
    padding: 10px;
    border: none;
    border-radius: 4px;
}

.form-container .contact-me .submit-btn {
    width: 200px;
    height: 50px;
}

.form-container .contact-me .submit-btn input {
    width: inherit;
    height: inherit;
    border: none;
    border-radius: 5px;
}

.form-container .contact-me .submit-btn input:hover {
    background-color: #FFB72B;
    border: 2px solid #000;
}

@media screen and (max-width: 768px) {
    .contact .contactHead {
        font-size: 4.5rem;
    }

    .contact-content .social-lnk {
        width: 3.5rem;
        height: 3.5rem;
        margin: 3px;
    }

    .form-container .contact-me .row .column {
        margin: 0 20px 40px 20px;
    }

    .cl-1 {
        margin-right: 20px!important;
    }
    
    .cl-2 {
        margin-left: 20px!important;
    }

    .form-container .contact-me .row .column .form-group label {
        font-size: 1rem;
    }

    .form-container .contact-me .row .column .form-group input {
        height: 40px;
        width: 180px;
        padding: 0 10px;
    }

    .form-container .contact-me .row .column .form-group textarea {
        width: 220px;
        height: 133px;
        padding: 10px;
    }

    .form-container .contact-me .submit-btn {
        width: 160px;
        height: 35px;
    }

    .form-container .contact-me .submit-btn input {
        font-size: 0.88rem;
    }
}

@media screen and (max-width: 530px) {
    .contact .contactHead {
        font-size: 2.5rem;
    }

    .contact-content .social-lnk {
        width: 2rem;
        height: 2rem;
        margin: 3px;
    }

    .form-container .contact-me .row .column {
        margin: 0 10px 20px 10px;
    }

    .cl-1 {
        margin-right: 10px!important;
    }
    
    .cl-2 {
        margin-left: 10px!important;
    }

    .form-container .contact-me .row .column .form-group label {
        font-size: 0.8rem;
    }

    .form-container .contact-me .row .column .form-group input {
        height: 30px;
        width: 140px;
        padding: 0 10px;
    }

    .form-container .contact-me .row .column .form-group textarea {
        width: 180px;
        height: 108px;
        padding: 10px;
    }

    .form-container .contact-me .submit-btn {
        width: 120px;
        height: 30px;
    }

    .form-container .contact-me .submit-btn input {
        font-size: 0.7rem;
    }
}

@media screen and (max-width: 375px) {
    .inner-pg-04 {
        padding: 5rem 0;
    }
}

@media screen and (max-width: 320px) {
    .inner-pg-04 {
        width: 100vw;
    }

    .form-container .contact-me .row .column .form-group label {
        font-size: 0.7rem;
    }

    .form-container .contact-me .row .column .form-group input {
        height: 25px;
        width: 100px;
        padding: 0 10px;
    }

    .form-container .contact-me .row .column .form-group textarea {
        width: 140px;
        height: 95px;
        padding: 10px;
    }

    .form-container .contact-me .submit-btn {
        width: 100px;
        height: 25px;
    }

    .form-container .contact-me .submit-btn input {
        line-height: 1.5rem;
        font-size: 0.65rem;
    }
}


