.cursor {
    fill: #FFDAC7;
    position: fixed;
    top: 0;
    left: 0;
    display: block;
    pointer-events: none;
    transition: all 0.5s forwards;
}

.scaleUp {
    animation: scalingUp 0.3s forwards;
}

.scaleDown {
    animation: scalingDown 0.3s forwards;
}

@keyframes scalingUp {
    From {
        transform: scale(1);
        opacity: 1;
    }
    To {
        transform: scale(4);
        opacity: 0.5;
    }
}

@keyframes scalingDown {
    From {
        transform: scale(4);
        opacity: 0.5;
    }
    To {
        transform: scale(1);
        opacity: 1;
    }

}
